import React from 'react';
import { Line } from 'react-chartjs-2';
import axios from 'axios';

if (!Array.prototype.last) {
  Array.prototype.last = function(){
    return this[this.length - 1];
  };
};

Date.prototype.getMonthNameShort = function(lang) {
  lang = lang && (lang in Date.locale) ? lang : 'en';
  return Date.locale[lang].month_names_short[this.getMonth()];
};

Date.locale = {
  en: {
    month_names: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
    month_names_short: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
  }
};

var CURRENCIES = ['ETH', 'ETC', 'XBT', 'XETCXETH', 'ZEC', 'LTC', 'XMR', 'BCH', 'DASH'];

var App = React.createClass({

  _krakenData: function(cur) {
    var MAX = 24*14;
    var pair = (cur === 'DASH' || cur === 'BCH') ? cur + "USD"
      : cur.length <= 3 ? "X" + cur + "ZUSD"
      : ''+cur;
    var krakenURl = "https://api.kraken.com/0/public/OHLC?interval=60&pair=" + pair;
    axios.get("https://myapis-1470585854240.appspot.com/?url=" + encodeURIComponent(krakenURl))
      .then(response => {
        var arr = response.data.result[pair];
        var slice = (arr.length >= MAX) ? arr.slice(arr.length - MAX) : (new Array(MAX - arr.length)).concat(arr);
        var timeClose = slice.map(x => [new Date(x[0]*1000), parseFloat(x[4])]);
        var st = {};
        st[cur] = timeClose;
        this.setState(st);
      })
      .catch(err => {
        console.error(pair, err.toString());
      });
  },

  loadData: function() {
    for (let i = 0; i < CURRENCIES.length; i++) {
      setTimeout(this._krakenData.call(this, CURRENCIES[i]), Math.floor(i/3) * 2000);
    }
  },

  getInitialState: function() {
    var st = {};
    for (let cur of CURRENCIES) {
      st[cur] = [];
    }
    return st;
  },

  componentDidMount: function() {
    this.loadData();
  },

  _price: function(cur, timelines) {
    var res = '?';
    var timeline = timelines[cur];
    if (timeline !== undefined) {
      var last = timeline.last();
      if (last) {
        res = last[1].toFixed(2) + '$';
      }
      if (cur == 'ETC' && timelines['XETCXETH']) {
        var ratio = timelines['XETCXETH'].last();
        if (ratio) {
          res += ' ('+ratio[1].toFixed(4)+' ETH)';
        }
      }
    }
    return cur + ": " + res;
  },

  render: function() {

    var MUL = {
      ETH: 1.0,
      ETC: 15.0,
      XBT: 1.0/20.0,
      BCH: 1.0/10.0,
      XETCXETH: 200.0,
      XMR: 3.0,
      ZEC: 1.0,
      DASH: 0.5,
      LTC: 3.0,
    };

    var COLOR = {
      // http://www.color-hex.com/color-palette/35605
      ETH: "#2069c9",
      ETC: "#69c920",
      XMR: '#a1dbff',
      XETCXETH: '#000',

      XBT: "#bbb",
      BCH: "#eee",
      ZEC: '#c92069',
      DASH: '#c98020',
      LTC: '#ffc5a1',
    };

    var allCurrencies = {
      ETH: this.state.ETH,
      ETC: this.state.ETC,
      XBT: this.state.XBT,
      BCH: this.state.BCH,
      XETCXETH: this.state.XETCXETH,
      XMR: this.state.XMR,
      ZEC: this.state.ZEC,
      DASH: this.state.DASH,
      LTC: this.state.LTC,
    };

    var graph = null;
    if (CURRENCIES.some( cur => allCurrencies[cur].length > 0 )) {

      var options = {
        responsive: true,
        maintainAspectRatio: false,

        tooltips: {
          mode: 'label',
          callbacks: {
            'label': function(tooltipItem, data) {
              var index = tooltipItem.index;
              var datasetIndex = tooltipItem.datasetIndex;
              var dataset = data.datasets[datasetIndex];
              var cur = dataset.label;
              var len = cur == 'XETCXETH' ? 4 : 2;
              var unit = cur == 'XETCXETH' ? '' : '$';
              return dataset.label + ": " + (allCurrencies[cur][index][1]).toFixed(len) + unit;
            }
          }
        },

        elements: {
          line: {
            fill: false,
          },
          point: {
            radius: 0,
            hitRadius: 10,
          },
        },
      };

      var labels = null;
      var datasets = [];
      for (let cur of CURRENCIES) {
        if (labels === null) {
          labels = allCurrencies[cur].map( (x, i) => i % 24 != 0 ? '' : x[0].getDate() + ' ' + x[0].getMonthNameShort().toUpperCase() );
        }

        var color = COLOR[cur] || '#ddd';
        var mul = MUL[cur] || 1.0;
        if (cur.length <= 4) {
          datasets.push({
            label: cur,
            borderColor: color,
            borderWidth: cur == 'XETCXETH' ? 1 : 2,
            data: allCurrencies[cur].map( x => x[1] * mul ),
          });
        }
      }
      var data = {
        labels: labels,
        datasets: datasets,
      };

      graph = <Line data={data} options={options} />
    }

    var REAL_CUR = CURRENCIES.filter( x => x.length <= 4)
    var title = REAL_CUR.join(', ');
    var prices = [];
    for (let cur of REAL_CUR) {
      prices.push(<h2 key={cur}>{this._price(cur, allCurrencies)}</h2>)
    }

    return (
      <div className="main">
        <h1>Price of ETH, ZEC, XBT, ...</h1>
        {prices}
        {graph}
      </div>
    );
  }
});

export default App;
